.customer-reviews {
    padding-top: 120px;
    padding-bottom: 130px;

    background-color: tomato;
}

.customer-reviews__container {
    // margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.customer-reviews__img {
    display: block;
    margin: 0 auto;
}

.customer-reviews__text {
    display: block;
    background-color: $backcolor;

    margin-top: 15px;
    margin-bottom: 34px;

    padding: 32px 54px 32px 50px;

    width: 926px;
    height: 138px;

    font-size: 16px;
    line-height: calc(30 / 16);
    text-align: center;
    letter-spacing: 0.04em;
}

.customer-reviews__titl {
    font-size: 20px;
    line-height: calc(37 / 20);
    text-align: center; 
}